.theme-header {
  background: url(https://www.almondlee.cn/storage/006tKfTcgy1g1om8efw2nj307z04a40f.jpg) no-repeat;
  background-size: 100% 100%;
  height: 20vh;
  filter: blur(4px);
  //box-shadow: 0px 9px 7px 0px rgba(130, 130, 130, 0.4);
}

.header-text {
  font-size: 3em;
  color: white;
  position: absolute;
  top: 5vh;
  left: 10px;
  right: 10px;
  text-align: center;

}

.cell {
  list-style: none;
  border-radius: 10px;
  margin: 5vw;
  padding: 5vw 5vw 1vw;
  background-color: white;
  box-shadow: 0 5px 10px 0 rgba(174, 174, 174, 0.4);
  display: flex;
  align-items: center;

  .cell-index {
    width: 100%;

    .avatar-name {
      display: flex;
      justify-content: space-between;
      align-items: center;

      .name {
        margin-left: 2vw;
        font-size: 16px;
      }
    }

    .work_name {
      font-size: 16px;
    }

    .main-issue {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    .work_tpl {
      float: right;
      //font-size: x-small;
      padding: 0px 6px;
      border-left: 3px #91e9ec solid;
      color: #0085ff;
    }

    .work_money {
      margin-right: 1vw;
      font-size: 16px;
      //font-weight: bold;
      // display: inline-block;
      padding: 3px 10px;
      //border-radius: 50px;
      color: red;
      border-bottom: 2px solid pink;
      //background-color: rgba(255, 231, 166, 0.9);
    }

    .money {
      text-align: center;
      margin: 5px;
    }

    .btns-shit {
      text-align: center;

      button {
        margin: 2vw;
      }
    }
  }
}

.cell-disable{
  background-color: #f0f0f0;
}